import React from 'react';

import classes from './Box.module.css';

const Box = (props) => {
  const class_1 = [classes.boxSECTION_1];
  if(props.background){
    class_1.push(classes[props.background]);
  }

    return(<section className={class_1.join(' ')}>
              <article className={classes.boxARTICLE_2}>
              <span className={props.noBar ? '' : classes.boxSPAN_3} ></span>
                <div className={classes.boxDIV_4}>
                    {props.children}
                </div>
              </article>
    </section>);
};

export default Box;