import React, {Component} from 'react';
import classes from './App.module.css';
import axios from 'axios';
import Chart from "react-apexcharts";


import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faQuestionCircle
} from '@fortawesome/pro-solid-svg-icons';


import 'bootstrap/dist/css/bootstrap.min.css';


import SimpleBar from 'simplebar-react';
import 'simplebar/dist/simplebar.min.css';

import CardPresentation from './components/Cards/CardPresentation';
import QA from './components/QA/QA';

import Page1 from './components/Pages/Page1';
import Page2 from './components/Pages/Page2';
import Page3 from './components/Pages/Page3';

import SmallBox from './components/Box/SmallBox';


const scrollableNodeRef = React.createRef();

class App extends Component{
  state = {
    page: 1,
    alt: '',
    companies : null,
    allocation: null,
    spara : 1,
    nav: false
  }

  httpHandler = () => {
    console.log(typeof this.state.alt)
    axios.post('/portfolio/get', 
    {alt: this.state.alt}
    ).then((response) => {
      this.setState({
        companies: response.data.company,
        allocation: response.data.allocation,
        page: this.state.page + 1
      });
    });
  }

  pageHandler = (alt, spara) => {
    scrollableNodeRef['current'].scrollTop = 0;
    window.scrollTo(0,0);

    if(alt === "back"){
      this.setState({
        page: 1,
        alt: ''
      });
    }else{
      this.setState({
        page: alt,
        alt: this.state.alt + spara
      });
    }
  };

  usersHeight = () => {
      var myHeight = 0;
      if( typeof( window.innerWidth ) == 'number' ) {
        //Non-IE
        myHeight = window.innerHeight;
      } else if( document.documentElement && ( document.documentElement.clientWidth || document.documentElement.clientHeight ) ) {
        //IE 6+ in 'standards compliant mode'
        myHeight = document.documentElement.clientHeight;
      } else if( document.body && ( document.body.clientWidth || document.body.clientHeight ) ) {
        //IE 4 compatible
        myHeight = document.body.clientHeight;
      }
      return myHeight;
  }

  onClickSideBarNav = () => {
    console.log("yes?")
    this.setState({
      nav: this.state.nav ? false : true
    });
  }

  render(){

    let html = null;

    if(this.state.page === 1){
      html = <Page1 pageHandler={this.pageHandler.bind(this)} />;
    }else if(this.state.page === 2){
      html = <Page2 pageHandler={this.pageHandler.bind(this)}/>;
  }else if(this.state.page === 3){
      html = <Page3 pageHandler={this.pageHandler.bind(this)} />;
    }else if(this.state.page === 4){
      this.httpHandler();
    }else if(this.state.page === 5){
      const options = {
        series: this.state.allocation.allocation,
        labels: this.state.allocation.company,
      }
      html = (
        <div className="donut">
          <button onClick={() => {this.pageHandler('back')}} class="black">Gå tillbaka</button>
          <h1>Din portfölj</h1>
          <p>Den här portföljen är tagen från modern portfölje teori och som har filtrerats baserat på dina val i förgående steg.</p>
          <div className="divDonut">
            <h3>Enligt vår kalkyl ska du allokera dina tillgångar så här:</h3>
            <SmallBox>
              <Chart
                    options={options} 
                    series={options.series}
                    labels={options.labels}
                    type="donut" 
                    width="100%"
                    height="100%"
                />
              </SmallBox>
          </div>
          <h3>Vi rekommenderar att du sparar i minst {this.state.spara} år</h3>
          <h3>Här nedanför kan du läsa mer om de fonder vi har tagit fram åt dig:</h3>
            {this.state.companies.map(el => {
              return (<CardPresentation 
                key={el._id}
                productFee={el.productFee}
                risk={el.risk}
                name={el.name}
                description={el.description}
              />);
            })}
          </div>
      );
    }

    const navClass = [classes.normal];
    if(this.state.nav === false){
      navClass.push(classes.side);
    }

    return (
      <div className={classes.App}>
        <div>
        <div className={classes.container1}>
          <div className={navClass.join(' ')}>
             <SimpleBar style={{ maxHeight: this.usersHeight() , "padding-top": "75px"  }}>
             <h4>Frågor och svar</h4>
              <QA title="Hur mäts risken?"><p>Text text text</p></QA>
              <QA title="Hur allokeras tillgångarna?">Text text text</QA>
            </SimpleBar>
          </div>
          <div onClick={this.onClickSideBarNav}  className={classes.questionIcon}>
            <FontAwesomeIcon  icon={faQuestionCircle} size="4x" color="#63e6be"  /> 
          </div>
          <div className={[classes.box1, classes.item].join(' ')}>
            <SimpleBar style={{ maxHeight: this.usersHeight(), "padding-top": "25px","z-index": -100 }} id="test" scrollableNodeProps={{ ref: scrollableNodeRef }}>
              {html}
            </SimpleBar>
          </div>
          
        </div>
        
        </div>
      </div>
    );
  };
}


export default App;
