import React, {Component} from 'react';
import './Cards.css';
import {TweenLite} from "gsap/TweenMax";

class Cards extends Component {
    constructor(props){
        super(props);
        // reference to the DOM node
        this.myElement = null;
        // reference to the animation
        this.myTween = null;
      }


      animationHandler = () => {
          this.myTween = TweenLite.to(this.myElement, 0.5, {rotation:360});
          setTimeout(() => {
            this.myTween = TweenLite.to(this.myElement, 0.1, {x: 0}).play();
          }, 100)
      }
    

    render(){
        return(
            <div className="cards-list">
                
                <div ref={div => this.myElement = div} onClick={() => {
                    this.animationHandler();
                    setTimeout(() =>{
                        this.props.click(this.props.alt)
                    }, 500)
                }}>
                <div className="card 1">
                <div className="card_image"> <img src="https://i.redd.it/b3esnz5ra34y.jpg" alt=""/> </div>
                <div className="card_title title-white">
                    <p>{this.props.title}</p>
                </div>
                </div>
                </div>
                {this.props.info ? <div class="text"><p>{this.props.info}</p></div> : null}  
            </div> 
        );
    }

}

export default Cards;