import React from 'react';

import classes from './QA.module.css';

const QA = (props) => {

  return (
    <div style={{"margin-top":"25px"}}>
        <div className={classes.box}>
            <h5>{props.title}</h5>
        </div>
        <div className={classes.box2} >
         {props.children}
        </div>
    </div>
  );
};

export default QA;