import React from 'react';

import Cards from '../Cards/Cards';

import classes from './Progressbar.module.css';

const Page2 = (props) => (
    <div>
      <button onClick={() => { props.pageHandler(1) }} class="black">Gå tillbaka</button>
      <h1>Hur länge vill du spara?</h1>
      <div className={classes.container}>
          <ul className={classes.progressbar} >
              <li className={classes.active}>Sparhorisont</li>
              <li>Optimering</li>
              <li>Förslag</li>
           </ul>
      </div>
    <Cards title="No stress" click={() => {props.pageHandler(3,"1")}} info="Passar bra till sparande på minst 2 år."  />
    <Cards title="Avkastning" click={() => {props.pageHandler(3,"2")}} info="Passar bra till sparande på minst 3 år. Filtrerar på historisk avkastning på fonder."  />
    <Cards title="Låg risk" click={() => {props.pageHandler(3,"3")}} info="Passar bra till sparande av lägre än 1 år. Filtrerar på historisk standardavvikelse."  />
  </div>
);

export default Page2;