import React from 'react';


import Cards from '../Cards/Cards';

import classes from './Progressbar.module.css';

import BOX from '../Box/Box';

const Page3 = (props) => (
    <div>
      <button onClick={() => {props.pageHandler(2)}} class="black">Gå tillbaka</button>
      <h1>Optimera</h1>
      <div className={classes.container}>
          <ul className={classes.progressbar} >
              <li className={classes.active}>Sparhorisont</li>
              <li>Optimering</li>
              <li>Förslag</li>
           </ul>
      </div>
      <BOX><p id="lorem-ipsum">Vi har med hjälp av modern portfölje teori tagit fram två förslag till optimering. Minsta risk innebär att portföljen optimeras för att nå en sådan låg risk som möjligt. En effektiv portfölj optimeras till en portfölj med högre risk men eventuellt högre avkastning.</p>
      </BOX>
      <Cards title="Minsta risk" click={() => props.pageHandler(4,"r")} alt="r" />
      <Cards title="Mest effektiv" click={() => props.pageHandler(4,"e")} alt="e" />
    </div>
  );

export default Page3;