import React from 'react';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faFeatherAlt, 
  faFlowerTulip, 
  faPiggyBank, 
  faMoneyBillWave
} from '@fortawesome/pro-solid-svg-icons';

import { Container, Row, Col  } from 'react-bootstrap';

import Box from '../Box/Box';

const Page1 = (props) => (
    <div>
        <Container >
        <Row>

          <Col sm={4}> 
          <FontAwesomeIcon icon={faFeatherAlt} size="4x" color="#63e6be" /> 
          </Col>
          <Col sm={8}><h3 id="title">
                        Välkommen till portföljgeneratorn
                      </h3>
                      <p></p>
            </Col>
            <div className="line2"></div>
            <Col lg ={12}> 
            <Box>
                    <FontAwesomeIcon icon={faFlowerTulip} size="3x" />
                    <h4 style={{"margin-top":"10px"}}>Det ska vara enkelt att välja fonder</h4>
                    <p>Portföljgeneratorn hjälper dig att ta fram fonder för dig och ditt sparande.
                    </p>
                    <p>
                    <FontAwesomeIcon icon={faPiggyBank} size="2x" />
                      <span style={{'padding-left':"10px", "padding-right": "10px", "font-size":"30px"}}>+</span>
                    <FontAwesomeIcon icon={faMoneyBillWave} size="2x" />
                    </p>
                    <p>
                      Allting på den här webbplatsen är 100 % gratis och alla förslag är endast generella tips.
                      Den metod vi har valt att analysera fonderna på är
  med modern portfölj teori (den effektiva fronten) och ett performance index (Ulcer Index). Med dessa två verktyg och några andra,
  finns nu portföljgeneratorn till för alla som behöver råd och hjälp till att välja fonder och skapa en bättre portfölj!
                    </p>
            </Box>
            </Col>
            <Col lg={12}>
            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1440 320"><path fill="#7bdcb5" fill-opacity="1" d="M0,64L40,85.3C80,107,160,149,240,144C320,139,400,85,480,74.7C560,64,640,96,720,138.7C800,181,880,235,960,240C1040,245,1120,203,1200,170.7C1280,139,1360,117,1400,106.7L1440,96L1440,320L1400,320C1360,320,1280,320,1200,320C1120,320,1040,320,960,320C880,320,800,320,720,320C640,320,560,320,480,320C400,320,320,320,240,320C160,320,80,320,40,320L0,320Z"></path></svg>
            <Box noBar={true} background="green">
              <h4>Kraven på våra portföljer</h4>
                    <ul>
                    <li>Varje portfölj kommer att innehålla minst 5 tillgångar.</li>
                    <li>Varje tillgång i våra förslag kommer ha en avkastning över genomsnittet i urvalet sedan 4 år tillbaka.</li>
                    <li>Varje tillgång i våra förslag kommer ha en mindre standardavvikelse än genomsnittet i urvalet sedan 4 år tillbaka.</li>
                    <li>Alla tillgångar som kommer med i förslagen på den här hemsidan har ett performance index över genomsnittet sedan 4 år tillbaka.</li>
                    <li>Tillgångarna i portföljen har allokerats baserat på modern portfölj teori.</li>
                    </ul>
                    <button onClick={() => { props.pageHandler(2) }} >Till portföljen</button>
              </Box>
            </Col>
        </Row>
        </Container>
        </div>
);

export default Page1;